  constructor() { }

  public init = () => {
    this.positions = new Float32Array(this.nParticles * 4);
    this.velocities = new Float32Array(this.nParticles * 4);

    for (let i = 0; i < this.positions.length; i += 4) {
      for (let j = 0; j < 4; j++)
        this.positions[i + j] = 0;
    }
    for (let i = 0; i < this.positions.length; i += 4) {
      for (let j = 0; j < 4; j++)
        this.velocities[i + j] = (j === 3) ? 1e4 : 0;
    }
  }

  public add = () => {
    this.nParticles++;
    const tmp = new Float32Array(4);
    const size = this.nParticles * 4;
    for (let i = 0; i < 4; i++) this.velocities[i] = 5e2;
    (this.positions = new Float32Array(size)).set([...this.positions, tmp]);
    (this.velocities = new Float32Array(size)).set([...this.velocities, tmp]);
  }

  public remove = () => {
    this.nParticles--;
    const size = this.nParticles * 4;
    (this.positions = new Float32Array(size)).set([...this.positions.slice(0, size)]);
    (this.velocities = new Float32Array(size)).set([...this.velocities.slice(0, 0, size)]);
  }

