  public parseObjAndLoad(objData) {
  
    objData.objects.forEach((particle, i) => {
  
      this.positions[i * 4 + 0] = particle.position[0];  // [0] Object Coordinates
      this.positions[i * 4 + 1] = particle.position[1];
      this.positions[i * 4 + 2] = particle.position[2];
      this.positions[i * 4 + 3] = particle.mass;  // [3] Object Mass

      this.velocities[i * 4 + 0] = particle.velocity[0];  // [0] Object Velocities
      this.velocities[i * 4 + 1] = particle.velocity[1];
      this.velocities[i * 4 + 2] = particle.velocity[2];
      this.velocities[i * 4 + 3] = particle.radius;  // [3] Object Radius
    });
  
    if (objData?.cameraPosition) this.cameraPosition = objData.cameraPosition;
    if (objData?.delta) this.delta = objData.delta;
  }

  public parseJsonAndLoad(jsonString) {
    const parsedData = JSON.parse(jsonString);
    this.parseObjAndLoad(parsedData);
  }

  public loadJsonFile(url) {
    return fetch(url)
    .then(response => {
      if (!response.ok) {
        throw new Error('Network response was not ok');
      }
      return response.json();
    })
    .then(data => this.parseJsonAndLoad(JSON.stringify(data))) // Parsing the JSON data
    .catch(error => console.error('There was a problem with the fetch operation:', error));
  }

  public uploadJsonFile(url) {
    this.loadJsonFile(url)
  }

  // Function to download JSON data as a file
  public downloadJsonFile(filename = null) {
    const json = this.to_json();
    const blob = new Blob([json], { type: 'application/json' });
    const url = URL.createObjectURL(blob);

    let name;
    if (!filename) {
      let date = Date.now();
      let hour = new Intl.DateTimeFormat('utc', { hour: 'numeric' }).format(date);
      let min = new Intl.DateTimeFormat('utc', { minute: 'numeric' }).format(date);
      let sec = new Intl.DateTimeFormat('utc', { second: 'numeric' }).format(date);
      let year = new Intl.DateTimeFormat('en', { year: 'numeric' }).format(date);
      let month = new Intl.DateTimeFormat('en', { month: '2-digit' }).format(date);
      let day = new Intl.DateTimeFormat('en', { day: '2-digit' }).format(date);
      name = `gravity-gpu_scenario_${day}-${month}-${year}_${hour}-${min}-${sec}-`;
    }
    else name = filename

    const a = document.createElement('a');
    a.href = url;
    a.download = name;
    document.body.appendChild(a);
    a.click();

    URL.revokeObjectURL(url);
  }

  public to_obj() {
    const objects = new Array();

    for (let i = 0; i < this.nParticles; i++) {
      const first = i * 4,
        last = i * 4 + 3;
      const particle = {
        position: [... this.positions.slice(first, last)],
        velocity: [... this.velocities.slice(first, last)],
        mass: this.positions[last],
        radius: this.velocities[last],
      }
      objects.push(particle);
    }

    const result = {
      cameraPosition: this.cameraPosition,
      delta: this.delta,
      objects: objects,
    }

    return result
  }

  public to_json() {
    const result = this.to_obj();
    return JSON.stringify(result, null, 2)
  }

