// import 'material-icons/iconfont/material-icons.css';

import { Render } from './Render';
import { Data } from './Data';

// DEV: Changes watchdog
new EventSource('/esbuild').addEventListener('change', () => location.reload())

class App {

  data: Data;
  render: Render;

  private readonly menuSymbolLeft: string = '&#10061;'//'&#9921;';
  private readonly menuSymbolRight: string = '&#10061;' //'&#9776'; // '&#9776'; // '&#9671;';// '&#8801'; // '&#8251;';
  private readonly exitSymbol: string = '&#10005;';
  objData: any;

  constructor() {

    this.data = new Data();
    this.render = new Render();

    // Load template scenario [0]
    this.handleLoad(0)

    this.main();
  }

  private load = (id: number = 0) => this.data.scenarios[id].callback();

  private update = (reload: boolean = false) => this.render.setupScene(this.data, reload);

  private handleLoad = (id: number = 0, reload: boolean = false) => {
    this.load(id)
    this.update(reload);
  }

  public reload = (id: number = 0) => {
    this.data = new Data();
    // Load template scenario [0] or user scenario
    this.handleLoad(id, true);
  }

  private main() {
    this.renderUI();
  }

  private renderUI = () => {

    const root = document.createElement('div');
    root.id = 'root';

    // Function to initialize the dropdown
    const initDropdown = () => {

      const dropdown = document.createElement('select');
      Object.assign(dropdown, {
        id: 'scenarios-dropdown',
        className: 'scenarios-dropdown',
      });

      // Get keys from data.scenarios and create option elements
      this.data.scenarios.forEach((e: any, index: number) => {
        let option = document.createElement('option');
        option.value = index.toString();
        option.textContent = e.name;
        console.log('Detected scenario:', e.name);

        dropdown.appendChild(option);
      });

      return dropdown;
    }
    const dropdown = initDropdown();

    // Event listener handling selection changes
    dropdown.addEventListener('change', (event: any) => {
      console.log('Selected scenario value:', event.target.value);

      // Handle update
      this.reload(event.target.value);
    });


    // TODO: Handler fot reseting particles ghosting
    // const resetGhostingBtn = document.createElement('div');
    // resetGhostingBtn.id = 'reset-ghosting';
    // resetGhostingBtn.className = 'reset-ghosting-btn';
    // resetGhostingBtn.innerText = "Reset ghosting";

    // resetGhostingBtn.onclick = () => this.render.setupAndResetGhostingHandler();

    // document.body.appendChild(resetGhostingBtn);

    const counters = document.createElement('div');
    counters.className = 'counters';
    {
      // Counters container
      const countersContainer = document.createElement('div');
      countersContainer.className = 'counters-container';

      {
        const elapsed = document.createElement('div');
        elapsed.className = 'counter';
        {
          const label = document.createElement('div');
          label.className = 'label';
          label.innerHTML = "Elapsed (years)";
          elapsed.appendChild(label)
        }
        {
          this.render.elapsedContainer = document.createElement('div');
          this.render.elapsedContainer.className = 'value';
          elapsed.appendChild(this.render.elapsedContainer);
        }
        countersContainer.appendChild(elapsed);
      }

      {
        const gpuCycle = document.createElement('div');
        gpuCycle.className = 'counter';
        {
          const label = document.createElement('div');
          label.className = 'label';
          label.innerHTML = "GPU Cycle";
          gpuCycle.appendChild(label)
        }
        {
          this.render.gpuCycleDeltaTimeContainer = document.createElement('div');
          this.render.gpuCycleDeltaTimeContainer.className = 'value';
          gpuCycle.appendChild(this.render.gpuCycleDeltaTimeContainer);
        }
        countersContainer.appendChild(gpuCycle);
      }

      {
        const gpuStep = document.createElement('div');
        gpuStep.className = 'counter';
        {
          const label = document.createElement('div');
          label.className = 'label';
          label.innerHTML = 'GPU Step&nbsp;<i>&Delta;t</i>';
          gpuStep.appendChild(label)
        }
        {
          this.render.gpuStepDeltaTimeContainer = document.createElement('div');
          this.render.gpuStepDeltaTimeContainer.className = 'value';
          gpuStep.appendChild(this.render.gpuStepDeltaTimeContainer);
        }
        countersContainer.appendChild(gpuStep);
      }

      counters.appendChild(countersContainer);
      root.appendChild(counters);
    }

    const controls = document.createElement('div');
    controls.className = 'controls';
    {
      // Controls container
      const controlsContainer = document.createElement('div');
      controlsContainer.className = 'controls-container';

      {
        const itterationControlLabel = document.createElement('label');
        itterationControlLabel.innerHTML = 'GPU cycle: '

        const itterationControlContainer = document.createElement('input');
        Object.assign(itterationControlContainer, {
          id: 'itteration-control',
          type: 'range',
          className: 'itteration-control-container',
          min: 0.0001,
          max: 100,
          step: 0.00001,
          value: this.render.defaultItterationControlValue,
        });

        itterationControlContainer.onchange = (e: any) => this.render.itterationControlValue = e.target.value;

        controlsContainer.appendChild(itterationControlLabel);
        controlsContainer.appendChild(itterationControlContainer);
      }

      {
        const deltaControlLabel = document.createElement('label');
        deltaControlLabel.innerHTML = 'GPU step <i>&Delta;t</i>: '

        const deltaControlContainer = document.createElement('input');
        Object.assign(deltaControlContainer, {
          id: 'delta-control',
          type: 'range',
          className: 'delta-control-container',
          min: this.render.defaultDeltaControlValue, // its also min, because of precision issues
          max: 0.2,
          step: 0.00001,
          value: this.render.defaultDeltaControlValue,
        });

        deltaControlContainer.oninput = (e: any) => this.render.deltaControlValue = e.target.value;

        controlsContainer.appendChild(deltaControlLabel);
        controlsContainer.appendChild(deltaControlContainer);
      }

      controls.appendChild(controlsContainer);
      root.appendChild(controls);
    }

    {
      // Left sidebar
      const sidebarLeft = document.createElement('div');
      sidebarLeft.className = 'sidebar left-sidebar closed';

      {
        // Sidebar container

        const sidebarContainer = document.createElement('div');
        sidebarContainer.className = 'sidebar-container';

        const titleContainer = document.createElement('div');
        titleContainer.className = 'title';
        titleContainer.innerHTML = `<h3>${this.menuSymbolLeft} Elements</h3>`;
        sidebarContainer.appendChild(titleContainer);

        const layersContainer = this.createObjectInputs();
        sidebarLeft.appendChild(layersContainer);
        sidebarContainer.appendChild(layersContainer);
        
        sidebarLeft.appendChild(sidebarContainer);
      }

      const menuBtn = document.createElement('div');
      menuBtn.className = 'menu-btn menu-btn-left';
      const closeButton = document.createElement('div');
      closeButton.innerHTML = this.menuSymbolLeft;

      closeButton.onclick = () => {
        sidebarLeft.classList.toggle("closed");
        closeButton.innerHTML = sidebarLeft.classList.contains("closed") ? this.menuSymbolLeft : this.exitSymbol;
      }
      menuBtn.appendChild(closeButton);
      sidebarLeft.appendChild(menuBtn);

      root.appendChild(sidebarLeft);
    }

    {
      // Right sidebar
      const sidebarRight = document.createElement('div');
      sidebarRight.className = 'sidebar right-sidebar closed';

      {
        // Sidebar container

        const sidebarContainer = document.createElement('div');
        sidebarContainer.className = 'sidebar-container';

        const titleContainer = document.createElement('div');
        titleContainer.className = 'title';
        titleContainer.innerHTML = `<h3>${this.menuSymbolRight} Enviroment</h3>`;
        sidebarContainer.appendChild(titleContainer);

        const dropdownContainer = document.createElement('div');
        dropdownContainer.appendChild(dropdown);
        sidebarContainer.appendChild(dropdownContainer);

        const itterationControlContainer = document.createElement('div');
        {
          const itterationControlLabel = document.createElement('label');
          itterationControlLabel.innerHTML = 'GPU cycle: '

          const itterationControl = document.createElement('input');
          Object.assign(itterationControl, {
            id: 'itteration-control',
            type: 'range',
            className: 'itteration-control-container',
            min: 0,
            max: 1500,
            step: 10,
            value: this.render.defaultControlValue,
          });

          itterationControl.onchange = (e: any) => this.render.itterationControlValue = e.target.value;

          itterationControlContainer.appendChild(itterationControlLabel);
          itterationControlContainer.appendChild(itterationControl);
        }
        sidebarContainer.appendChild(itterationControlContainer);

        const deltaControlContainer = document.createElement('div');
        {
          const deltaControlLabel = document.createElement('label');
          deltaControlLabel.innerHTML = 'GPU step <i>&Delta;t</i>: '

          const deltaControl = document.createElement('input');
          Object.assign(deltaControl, {
            id: 'delta-control',
            type: 'range',
            className: 'delta-control-container',
            min: 1,
            max: 2500,
            step: 1,
            value: this.render.defaultControlValue,
          });

          deltaControl.oninput = (e: any) => this.render.deltaControlValue = e.target.value;

          deltaControlContainer.appendChild(deltaControlLabel);
          deltaControlContainer.appendChild(deltaControl);
        }
        sidebarContainer.appendChild(deltaControlContainer);

        sidebarRight.appendChild(sidebarContainer);
      }

      const menuBtn = document.createElement('div');
      menuBtn.className = 'menu-btn menu-btn-right';
      const closeButton = document.createElement('div');
      closeButton.innerHTML = this.menuSymbolRight;
      closeButton.onclick = () => {
        sidebarRight.classList.toggle("closed");
        closeButton.innerHTML = sidebarRight.classList.contains("closed") ? this.menuSymbolRight : this.exitSymbol;
      }
      menuBtn.appendChild(closeButton);
      sidebarRight.appendChild(menuBtn);

      root.appendChild(sidebarRight);
    }

    // &#9998; edit
    // &#10148; pointer
    document.body.appendChild(root);
  }

  public createObjectInputs() {
    const objectsContainer = document.createElement('div') as HTMLElement;
  
    // Clear previous content
    objectsContainer.innerHTML = '';
    objectsContainer.classList.add('objectsContainer');

    
    this.objData = this.data.to_obj();

    // Iterate over each object in the JSON data
    this.objData.objects.forEach((object, index) => {
      // Create a div element for the object
      const objectDiv = document.createElement('div');
      objectDiv.classList.add('object');

      const label = document.createElement('div');
      label.innerText = `Object ${(index + 1)}`;
      objectDiv.appendChild(label);
  
      // Create inputs for position, velocity, mass, and radius
      const positionInput = this.createInput('position', object.position, 'number');
      const velocityInput = this.createInput('velocity', object.velocity, 'number');
      const massInput = this.createInput('mass', object.mass, 'number');
      const radiusInput = this.createInput('radius', object.radius, 'number');
  
      // Append inputs to the object div
      objectDiv.appendChild(positionInput);
      objectDiv.appendChild(velocityInput);
      objectDiv.appendChild(massInput);
      objectDiv.appendChild(radiusInput);
  
      // Append the object div to the container
      objectsContainer.appendChild(objectDiv);
    });

    return objectsContainer;
  }
  
  // Function to create input elements
  private createInput(property, value, type) {
    
    const createInputElement = (prop, val) => {
      const input = document.createElement('input');
      input.setAttribute('type', type);
      input.setAttribute('value', val);
      input.setAttribute('data-property', prop);

      return input;
    }

    const eventHandler = (event: any, idx) => {
      const newValue = parseFloat(event.target.value);
      const property = event.target.getAttribute('data-property');
      const obj = event.target.closest('.object');
      const objectIndex = Array.prototype.indexOf.call(obj.parentNode.childNodes, obj); //event.target.parentNode.getAttribute('data-index');
      console.log(objectIndex)
      // Update the value in the JSON data
      if (idx !== -1) this.objData.objects[objectIndex][property][idx] = newValue
      else this.objData.objects[objectIndex][property] = newValue;
  
      // Print updated JSON data
      // console.log(jsonData);
      this.data.init()
      this.data.parseObjAndLoad(this.objData);
      const reload = true;
      this.update(reload);
    }

    const inputs = new Array();
    inputs.push(value);


    const div = document.createElement('div');
    inputs.forEach((input: any) => {
      const sub = document.createElement('div');
      const label = document.createElement('div');
      label.innerHTML = property;
      sub.appendChild(label);
      if (input.length > 1) 
        input.forEach((el, idx) => {
          const res = createInputElement(property, input[idx]);
          res.addEventListener('change', (event: any) => eventHandler(event, idx));
          // Add event listener to handle changes
          sub.appendChild(res);
        });
      else {
        const res = createInputElement(property, input);
        res.addEventListener('change', (event: any) => eventHandler(event, -1));
        // Add event listener to handle changes
        sub.appendChild(res);
      }
      div.appendChild(sub);
    }) 

    return div;
  }
}

const app = new App();
