  private clearScene(scene) {
    while (scene.children.length > 0) {
      const object = scene.children[0];
      if (object.isMesh) {
        if (object.geometry) object.geometry.dispose();
        if (object.material) {
          if (object.material.isMaterial) {
            this.cleanMaterial(object.material);
          } else {
            // Array of materials
            for (const material of object.material) this.cleanMaterial(material);
          }
        }
      }
      scene.remove(object);
    }
  }

  private cleanMaterial(material) {
    material.dispose();
    // Dispose textures if any
    for (const key of Object.keys(material)) {
      const value = material[key];
      if (value && typeof value === 'object' && 'dispose' in value) {
        value.dispose();
      }
    }
  }

  private disposeOldGPUComputationRenderer(gpuCompute) {
    if (!gpuCompute) {
      return;
    }
    // Loop through all variables and dispose of their render targets
    for (let i = 0; i < gpuCompute.variables.length; i++) {
      const variable = gpuCompute.variables[i];
      variable.renderTargets.forEach(target => {
        target.dispose();
      });
    }
  }
  
  private disposeTextures(textures) {
    textures.forEach(texture => {
      if (texture) {
        console.log('disposing texture:', texture)
        texture.dispose();
      }
    });
  }

